body {
  margin: 0;
  font-family: 'Segoe UI', sans-serif;
  background: #fafafa;
  color: #222;
}

.app {
  max-width: 768px;
  margin: 0 auto;
  padding: 16px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 16px;
}

.header h1 {
  margin: 0;
  font-size: 24px;
}

.header nav {
  display: flex;
  gap: 16px;
  align-items: center;
}

.header nav a {
  text-decoration: none;
  color: #222;
  font-weight: 500;
}

.cart {
  background: #eee;
  padding: 4px 10px;
  border-radius: 12px;
}

.search input {
  width: 100%;
  padding: 12px;
  font-size: 16px;
  border: none;
  border-radius: 10px;
  background: #f1f1f1;
  margin-bottom: 24px;
}

.products {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
}

.product-card {
  background: white;
  border-radius: 16px;
  padding: 16px;
  text-align: center;
  box-shadow: 0 1px 4px rgba(0,0,0,0.05);
}

.product-card img {
  width: 80px;
  height: 80px;
  object-fit: contain;
  margin-bottom: 8px;
}

.product-card h3 {
  margin: 8px 0 4px;
  font-size: 18px;
}

.product-card p {
  margin: 0 0 12px;
  font-size: 16px;
  font-weight: 500;
}

.product-card button {
  padding: 8px 12px;
  border: none;
  border-radius: 10px;
  background-color: #f1f1f1;
  cursor: pointer;
}

.checkout-btn {
  width: 100%;
  margin-top: 24px;
  padding: 14px;
  font-size: 16px;
  background-color: #197d86;
  color: white;
  border: none;
  border-radius: 12px;
  cursor: pointer;
}

.footer {
  margin-top: 32px;
  padding-top: 16px;
  border-top: 1px solid #ccc;
  display: flex;
  justify-content: space-between;
  font-size: 14px;
}
